.container {
    width: 80%;
    margin: auto;
    margin-top: 80px; }

a {
    text-decoration: none;
    &:hover {
        text-decoration: underline; } }

img.small {
    width: 50px;
    height: 50px;
    margin: 0px 5px; }

table.results {
    margin: auto;
    width: 100%;
    a {
        color: $soft-green;
        &:hover {
            color: lighten($soft-green, 2%); } }
    td {
        border-bottom: 1px solid $light-gray;
        a,
        img {
            display: inline-block;
            vertical-align: middle; } }
    thead tr {
        background-color: $soft-green;
        color: #FFF; }
    th,
    td {
        padding: 5px; } }

.actions {
    margin: 10px 5px; }



